import React, { lazy, Suspense } from "react";
import { Switch, Route, Redirect } from "react-router-dom";
import { connect } from 'react-redux';

import { adminRoutes, managerRoutes, agentRoutes, digitizerRoutes, designerRoutes } from 'configs/NavigationConfig';

import Loading from 'components/shared-components/Loading';

export const AppViews = (props) => {
  let { role } = props

  let navigationConfig = null;
  if (role === 'admin') {
    navigationConfig = adminRoutes;
  } else if (role === 'manager') {
    navigationConfig = managerRoutes;
  } else if (role === 'agent') {
    navigationConfig = agentRoutes;
  } else if (role === 'designer') {
    navigationConfig = designerRoutes;
  } else if (role === 'digitizer') {
    navigationConfig = digitizerRoutes;
  }

  return (
    <Suspense fallback={<Loading cover="content" />}>
      <Switch>
        {navigationConfig.map(item => {
          return <Route path={item.path} exact component={lazy(() => import(`./${item.component}`))} key={item.key} />
        })}
        <Redirect to='not-found' />
      </Switch>
    </Suspense>
  )
}

const mapStateToProps = state => ({
  role: state.auth.user.role_name
});

export default connect(mapStateToProps)(React.memo(AppViews));
