import { message } from 'antd';
import axios from 'axios';
import {
    LOGIN, STOP_AUTH_LOADING, LOGOUT
} from '../constants';

export const checkAuth = () => async dispatch => {
    let user = await localStorage.getItem('user');
    user = await JSON.parse(user);
    if (user) {
        dispatch({
            type: LOGIN,
            payload: user
        });
    } else {
        dispatch({
            type: STOP_AUTH_LOADING
        })
    }

}

export const login = (data, setLoading) => async dispatch => {
    try {
        let res = await axios.post('/login', data);
        setLoading(false);
        await localStorage.setItem('user', JSON.stringify(res.data.data));
        dispatch({
            type: LOGIN,
            payload: res.data.data
        });
        message.success(res.data.message, 3);
    } catch (error) {
        console.log(error);
        if (error.response) {
            message.error(error.response.data, 3);
        } else {
            message.error('Server not responding', 3);
        }
        setLoading(false);
    }
}

export const logout = () => async (dispatch, getState) => {
    try {
        let user_id = getState().auth.user.user_id;
        let res = await axios.post('/logout', { user_id });
        await localStorage.removeItem('user');
        dispatch({
            type: LOGOUT,
        });
        message.success(res.data.message);
    } catch (error) {
        console.log(error);
        if (error.response) {
            message.error(error.response.data);
        } else {
            message.error('Server not responding');
        }
    }
}