import React from 'react';
import { Layout, Grid } from 'antd';
import { connect } from 'react-redux';
import SideNav from 'components/layout-components/SideNav';
import TopNav from 'components/layout-components/TopNav';
import MobileNav from 'components/layout-components/MobileNav'
import HeaderNav from 'components/layout-components/HeaderNav';
import PageHeader from 'components/layout-components/PageHeader';
import Footer from 'components/layout-components/Footer';
import AppViews from 'views/app-views';
import ErrorPage from '../../views/auth-views/errors/error-page-1';
import { adminRoutes, managerRoutes, agentRoutes, designerRoutes, digitizerRoutes } from "configs/NavigationConfig";
import {
  SIDE_NAV_WIDTH,
  SIDE_NAV_COLLAPSED_WIDTH,
  NAV_TYPE_SIDE,
  NAV_TYPE_TOP
} from 'constants/ThemeConstant';
import utils from 'utils';

const { Content } = Layout;
const { useBreakpoint } = Grid;

export const AppLayout = ({ navCollapsed, navType, location, role }) => {
  let navigationConfig = null;
  if (role === 'admin') {
    navigationConfig = adminRoutes;
  } else if (role === 'manager') {
    navigationConfig = managerRoutes;
  } else if (role === 'agent') {
    navigationConfig = agentRoutes;
  } else if (role === 'designer') {
    navigationConfig = designerRoutes;
  } else if (role === 'digitizer') {
    navigationConfig = digitizerRoutes;
  }

  const currentRouteInfo = utils.getRouteInfo(navigationConfig, location.pathname);
  const screens = utils.getBreakPoint(useBreakpoint());
  const isMobile = !screens.includes('lg')
  const isNavSide = navType === NAV_TYPE_SIDE
  const isNavTop = navType === NAV_TYPE_TOP
  const getLayoutGutter = () => {
    if (isNavTop || isMobile) {
      return 0
    }
    return navCollapsed ? SIDE_NAV_COLLAPSED_WIDTH : SIDE_NAV_WIDTH
  }

  // return (
  //   <Layout>
  //     <HeaderNav isMobile={isMobile} />
  //     {(isNavTop && !isMobile) ? <TopNav routeInfo={currentRouteInfo} /> : null}
  //     <Layout className="app-container">
  //       {(isNavSide && !isMobile) ? <SideNav routeInfo={currentRouteInfo} /> : null}
  //       <Layout className="app-layout" style={{ paddingLeft: getLayoutGutter() }}>
  //         <div className={`app-content ${isNavTop ? 'layout-top-nav' : ''}`}>
  //           <PageHeader display={currentRouteInfo?.breadcrumb} title={currentRouteInfo?.title} />
  //           <Content>
  //             <AppViews />
  //           </Content>
  //         </div>
  //         <Footer />
  //       </Layout>
  //     </Layout>
  //     {isMobile && <MobileNav />}
  //   </Layout>
  // )

  if (location.pathname.match(/not-found/)) {
    return (
      <div className="h-100 bg-white">
        <ErrorPage />
      </div>
    )
  } else {
    return (
      <Layout>
        <HeaderNav isMobile={isMobile} />
        {(isNavTop && !isMobile) ? <TopNav routeInfo={currentRouteInfo} /> : null}
        <Layout className="app-container">
          {(isNavSide && !isMobile) ? <SideNav routeInfo={currentRouteInfo} /> : null}
          <Layout className="app-layout" style={{ paddingLeft: getLayoutGutter() }}>
            <div className={`app-content ${isNavTop ? 'layout-top-nav' : ''}`}>
              <PageHeader display={currentRouteInfo?.breadcrumb} title={currentRouteInfo?.title} />
              <div style={{ height: '100%' }}>
                <AppViews />
              </div>
            </div>
            <Footer />
          </Layout>
        </Layout>
        {isMobile && <MobileNav />}
      </Layout>
    )
  }
}

const mapStateToProps = state => ({
  navCollapsed: state.theme.navCollapsed,
  navType: state.theme.navType,
  locale: state.theme.locale,
  role: state.auth.user.role_name
});

export default connect(mapStateToProps)(React.memo(AppLayout));