import {
	LOGIN,
	STOP_AUTH_LOADING,
	LOGOUT
} from '../constants';

const initState = {
	authLoading: true,
	isAuthenticated: false,
	user: null
}

const auth = (state = initState, action) => {
	let { type, payload } = action;
	switch (type) {
		case LOGIN:
			return {
				...state,
				isAuthenticated: true,
				user: payload,
				authLoading: false
			}
		case STOP_AUTH_LOADING:
			return {
				...state,
				authLoading: false
			}
		case LOGOUT:
			return {
				...state,
				authLoading: false,
				isAuthenticated: false,
				user: null
			}
		default:
			return state;
	}
}

export default auth;