import React from "react";
import { Route, Switch, Redirect, withRouter } from "react-router-dom";
import { connect } from "react-redux";
import AppLayout from "layouts/app-layout";
import AuthLayout from 'layouts/auth-layout';
import AppLocale from "lang";
import { IntlProvider } from "react-intl";
import { ConfigProvider } from 'antd';
import { AUTH_PREFIX_PATH } from 'configs/AppConfig';
import PrivateRoute from './PrivateRoute';

import { checkAuth } from 'redux/actions/AuthActions';

export const Views = (props) => {
  const { locale, location, checkAuth } = props;
  const currentAppLocale = AppLocale[locale];

  React.useEffect(() => {
    checkAuth();
  }, []);

  return (
    <IntlProvider
      locale={currentAppLocale.locale}
      messages={currentAppLocale.messages}>
      <ConfigProvider locale={currentAppLocale.antd}>
        <Switch>
          <Route exact path="/">
            <Redirect to='/home' />
          </Route>
          <Route path={AUTH_PREFIX_PATH} component={AuthLayout} />
          <PrivateRoute path='/' component={() => <AppLayout location={location} />} />
        </Switch>
      </ConfigProvider>
    </IntlProvider>
  )
}


const mapStateToProps = state => ({
  locale: state.theme.locale,
});

export default withRouter(connect(mapStateToProps, { checkAuth })(Views));